.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.heart {
  margin: 100px auto;
  display: flex;
  justify-content: center;
}

.h {
  width: 100px;
  height: 100px;
  transform: rotate(45deg);
  background-color: #f00;
  position: absolute;
}

.h1 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #f00;
  transform: rotate(45deg);
  position: relative;
  left: 15px;
  top: -35px;
  animation: redHeart1 4s ease-in-out infinite;
}

.h2 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #f00;
  transform: rotate(45deg);
  position: relative;
  left: -15px;
  top: -35px;
  animation: redHeart2 4s ease-in-out infinite;
}

@keyframes redHeart1 {
  0% {
    left: 15px;
    top: -35px;
  }

  25% {
    left: 85px;
    top: 35px;
  }

  50% {
    left: 85px;
    top: 35px;
  }

  75% {
    left: 15px;
    top: -35px;
  }

  100% {
    left: 15px;
    top: -35px;
  }
}

@keyframes redHeart2 {
  0% {
    left: -15px;
    top: -35px;
  }

  25% {
    left: -15px;
    top: -35px;
  }

  50% {
    left: -85px;
    top: 35px;
  }

  75% {
    left: -85px;
    top: 35px;
  }

  100% {
    left: -15px;
    top: -35px;
  }
}
